import { Container, Content } from "./styles";
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';
import SearchIcon from '@material-ui/icons/Search';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Badge from '@material-ui/core/Badge';
import { useHistory } from "react-router";

export default function Header() {

  const today = new Date();
  const history = useHistory();

  function handleNavigateToPage(route: string): void {
    history.push(route);
  }

  return (
    <Container>
      <Content>
        <div className="itemMenuBar title">
          <p className="title-label" >Garage845</p>
        </div>
        <div className="itemMenuBar date">
          <p className="label">hoje</p>
          <p>{`${today.getDate()} ${today.toLocaleString('default', { month: 'long' })}, ${today.getFullYear()}`}</p>
        </div>
        <p className="itemMenuBar vendas">vendas</p>
        <div className="itemMenuBar search">
          <SearchIcon className="search-icon"
            onClick={() => handleNavigateToPage('search')}></SearchIcon>
        </div>
        <div className="itemMenuBar alert">
          <Badge badgeContent={4} color="error">
            <NotificationsNoneIcon className="alert-icon"></NotificationsNoneIcon>
          </Badge>
        </div>
        <div onClick={() => handleNavigateToPage('profile')} className="itemMenuBar user">
          <p>Cynthia Souza</p>
          <ExpandMoreIcon></ExpandMoreIcon>
        </div>
      </Content>
    </Container>
  );
}
