import { useState } from "react";
import { useHistory } from "react-router-dom";
import { Container, Content, MenuItem } from "./styles";
import menu from '../../utils/menu.json';

interface Option {
  url: string,
  label: string,
  position: number
}

export default function MenuBar() {

  const options: Option[] = menu;
  const [clickPosition, setClickPosition] = useState(0);
  const history = useHistory();

  function handleActiveMenuItem(option: Option, index: number): void {
    setClickPosition(index);
    history.push(option.url);
  }

  return (
    <Container>
      <Content>
        {options.map((option, index) =>
          <MenuItem key={index} className="menuItem"
            active={clickPosition === index} onClick={() => handleActiveMenuItem(option, index)}>
            {option.label}
          </MenuItem>)}
      </Content>
    </Container>
  );
}