import { useState } from "react";
import styled, { keyframes } from "styled-components";
import FormButton from "../../../components/formButton";
import Input from "../../../components/input";

const containerAnimation = keyframes`
  from { opacity: 0 }
  to { opacity: 1 };
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const Content = styled.div`
  display: flex;
  animation-name: ${containerAnimation};
  animation-duration: 1s;

  .about {
    width: 50%;
    display: flex;
    box-sizing: border-box;
  }

  .form {
    width: 50%;
  }
`;

const Control = styled.div`
  display: flex;
`;

interface Props {
  cancelCadastro: any;
}

export default function Cadastro(props: Props) {

  const [titulo, setTitulo] = useState('');
  const [descricao, setDescricao] = useState('');

  function loadProduto() {
    return {
      'titulo': titulo,
      'descricao': descricao
    }
  }

  function handleSalvaProduto() {
    const produto = loadProduto();
    console.log(produto);
    console.log('salvando...');
  }

  return (
    <Container>
      <Content>
        <div className="about">
          <p>ajuda</p>
        </div>
        <div className="form">

          <Input onChange={(event: any) => setTitulo(event.target.value)}
            value={titulo} type={'text'}
            label={'titulo'} />

          <Input onChange={(event: any) => setDescricao(event.target.value)}
            value={descricao} type={'textarea'}
            label={'descrição'} />

          <Control>
            <FormButton label={'salvar'} action={handleSalvaProduto} />
            <FormButton label={'cancelar'} cancel action={props.cancelCadastro} />
          </Control>

        </div>
      </Content>
    </Container>
  );
}
