import styled from "styled-components";

interface IProps {
  cancel: boolean;
}

const Container = styled.div<IProps>`
    display: flex;

    button {
      width: 120px;
      margin-right: 10px;
      height: 40px;
      background-color: ${({ cancel }) => cancel ? '#ffffff' : '#00a100'};
      border: 1px solid #969696;
      border-radius: 5px;
      color: ${({ cancel }) => cancel ? '#5c5c5c' : '#ffffff'};
      font-size: 12px;

      -webkit-transition-duration: 0.4s; /* Safari */
      transition-duration: 0.4s;
    }

    button:hover {
      background-color: ${({ cancel }) => cancel ? '#eeeeee' : '#008f00'};
    }

    button:active {
      background-color: ${({ cancel }) => cancel ? '#d4d4d4' : '#007a00'};
    }
`;

export default function FormButton(props: any) {

  return (
    <Container cancel={props.cancel}>
      <button onClick={() => props.action()}>{props.label}</button>
    </Container>
  );
}
