import styled from "styled-components";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const Content = styled.div`
  width: 100%;
  height: 600px;
  margin-bottom: 40px;
  background-color: #ffffff;
  border: 1px solid #cacaca;
  border-radius: 10px;
`;

export default function Ofertas() {
  return (
    <Container>
      <div>
        <p>Ofertas</p>
        <Content></Content>
      </div>
    </Container>
  );
}