import Produto from "../../../models/produto";
import { Content, Label, Data, Container } from "./styles";

interface IProps {
  data: Produto[];
}

export default function Datasheet(props: IProps) {

  function formatDate(date: Date) {
    return `
      ${date.getDate()}
      ${date.toLocaleString('default', { month: 'long' })},
      ${date.getFullYear()}`;
  }

  return (
    <Container>
      {props.data.map((data: Produto, index: number) => (
        <Data key={index}>
          <Content>
            <div className="unit info">
              <Label>info</Label>
              <p className="content">{data.titulo}</p>
              <p className="details">{data.descricao}</p>
            </div>
            <div className="status">
              <div>
                <Label>status</Label>
                <p>{data.status.descricao}</p>
              </div>
              <div>
                <Label>atualizado em</Label>
                <p>{formatDate(data.updateAt)}</p>
              </div>
              <div>
                <Label>categorias</Label>
                <p>canecas</p>
              </div>
            </div>
            <div className="unit actions">
              <Label>ações</Label>
              <div className="action-area-button">
                <button>apagar</button>
              </div>
            </div>
          </Content>
        </Data>
      ))}
    </Container>
  )
}
