import { useState } from "react";
import Produto from "../../models/produto";
import Cadastro from "./Cadastro";
import Datasheet from "./Datasheet";
import { Button, Container, Content, Control } from "./styles";

export default function Produtos() {

  const [isListagem, setListagem] = useState(true);
  const [isCadastro, setCadastro] = useState(false);

  const produtos: Produto[] = [];

  const produto: Produto = {
    produtoId: 1,
    titulo: "Caneca Star wars",
    descricao: 'Caneca Star wars bb8',
    updateAt: new Date(2021, 4, 15),
    status: {
      codigo: 1,
      descricao: 'ativo'
    },
  }

  produtos.push(produto);
  const produto2 = { ...produto };

  produto2.status = {codigo: 2, descricao: 'inativo'};
  produto2.titulo = 'Chaveiro Iron Man';
  produto2.descricao = 'Chaveiro Iron Man de aluminio resistente';
  produtos.push(produto2);

  function handleChangeContentToCadastro() {
    setListagem(false);
    setCadastro(true);
  }

  function handleChangeContentToListagem() {
    setCadastro(false);
    setListagem(true);
  }

  return (
    <Container>
      <Content>
        <Control>
          <div>
            <p className="control-label">Produtos</p>
            <p className="control-subtitle">Use esta tela para administrar seus produtos</p>
          </div>
          <Button onClick={() => handleChangeContentToCadastro()}>novo produto</Button>
        </Control>
        {isListagem && <Datasheet data={produtos} />}
        {isCadastro && <Cadastro cancelCadastro={handleChangeContentToListagem} />}
      </Content>
    </Container>
  );
}
