import styled from "styled-components";

interface InputPros {
  label: string;
  type: string;
  onChange: any;
  value: any;
}

const Container = styled.div`
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

    .label {
      width: 25%;
      display: flex;
      font-family: 'Roboto';
      font-weight: 500;
      font-size: 14px;
      text-transform: capitalize;
      padding-right: 10px;
      box-sizing: border-box;
    }

    .field {
      display: flex;
      width: 70%;
      margin-bottom: 12px;
      box-sizing: border-box;

      input {
        border-radius: 5px;
        border: 1px solid #969696;
        padding-left: 6px;
        width: 340px;
        height: 40px;
        box-sizing: border-box;
      }

      textarea {
        border-radius: 5px;
        border: 1px solid #969696;
        padding-top: 10px;
        padding-left: 6px;
        width: 340px;
        max-width: 340px;
        min-width: 340px;
        min-height: 40px;
        font-family: 'Roboto';
        box-sizing: border-box;
      }
    }
`;

export default function Input(props: InputPros) {

  const type = props.type;
  const onChange = props.onChange;
  const value = props.value;

  return (
    <Container>
      <div className="label">
        <p>{props.label}</p>
      </div>
      <div className="field">
        {type !== 'textarea' && <input onChange={event => onChange(event)} value={value} type={type} />}
        {type === 'textarea' && <textarea onChange={event => onChange(event)} value={value}></textarea>}
      </div>
    </Container>
  );
}
